.topNav {
    display: grid;
    grid-template-columns: auto 40vh 25vw;  
    height: 6vh;
    align-items: center;
    background-color:	#e5e5e5;
    border-radius: 0 0 10px 10px;
    border-bottom: 1px solid red;
    vertical-align: middle;
}
.logo {
  margin: 0;
  height: 100%;
  width: 100%;
}
.topButtonsWarper{
  display: grid;
  justify-content: space-between;
}
.topButtons {
  font-weight: bold;
  font-size: 1.2em;
}
